import React, { useState } from 'react';
import { ReportProvider } from './components/context/ReportContext';
import MonthSelector from './components/MonthSelector';
import PageList from './components/PageList';
import AddPageModal from './components/AddPageModal';
import DownloadReportButton from './components/DownloadReportButton';
import { Button, Typography, Box, Paper } from '@mui/material';

const App = () => {
  const [editorOpen, setEditorOpen] = useState(false);

  const handleOpenEditor = () => setEditorOpen(true);
  const handleCloseEditor = () => setEditorOpen(false);

  return (
    <ReportProvider>
      <div className="app-container" style={{ padding: '16px', maxWidth: '800px', margin: '0 auto' }}>
        <Typography variant="h4" component="h1" align="center" gutterBottom>
          Generador de Reportes Operativos
        </Typography>
        
        {/* Contenedor de componentes */}
        <Box 
          sx={{ display: 'flex', flexDirection: 'column', gap: '16px' }}
        >
          {/* Selector de Mes */}
          <Paper elevation={3} sx={{ padding: '16px' }}>
            <MonthSelector />
          </Paper>
          
          {/* Botn para abrir el modal de agregar pgina */}
          <Box sx={{ display: 'flex', justifyContent: 'center' }}>
            <Button
              variant="contained" 
              color="primary"
              onClick={handleOpenEditor}
            >
              Agregar Pgina
            </Button>
          </Box>
          
          {/* Modal de agregar pgina */}
          <AddPageModal open={editorOpen} onClose={handleCloseEditor} />
          
          {/* Lista de pginas creadas */}
          <Paper elevation={3} sx={{ padding: '16px' }}>
            <PageList />
          </Paper>
          
          {/* Botn para descargar el reporte */}
          <Box sx={{ display: 'flex', justifyContent: 'center' }}>
            <DownloadReportButton />
          </Box>
        </Box>
      </div>
    </ReportProvider>
  );
};

export default App;
